.container {
    text-align: center;
    padding: 20px;
    background-color: #f0f0f0;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 800px;
    margin: 100px auto;
}

.title {
    font-size: 2.5rem;
    color: #333;
    margin-bottom: 10px;
}

.description {
    font-size: 1.2rem;
    color: #666;
    line-height: 1.6;
}

.span {
    color: #474747;
    font-weight: normal;
    font-size: 2rem;
}